//get items from axios
import axios from "axios";
import { useEffect, useState } from "react";
type ItemType = {
    id: string;
    name: string;
    description: string;
    price: number;
    image: string;
};
const fetch = "http://localhost:8080/api/menu/getAllMenu";
export default function GetItems() {
    const [items, setItems] = useState<ItemType[]>([]);
    useEffect(() => {
        axios.get(fetch).then((res) => {
            setItems(res.data);
            console.log(res.data);
        });
    }, []);
    return (
        <div>
            {items.map((item) => (
                <div key={item.id}>
                    <img src={item.image} alt="item" />
                    <h1>{item.name}</h1>
                    <p>{item.description}</p>
                    <p>{item.price}</p>
                </div>
            ))}
        </div>
    );
}
